import React from 'react';
import ActivityOrderImages from './ActivityOrderImages/index';
import ActivityImage from './ActivityImage';
import ActivityBoat from './ActivityBoat/index';
import ActivityMathProblem from './ActivityMathProblem';

const activities = [
    {
        desc: `Benvinguts i benvingudes al primer pis de l'expedici. En aquest espai veiem uns espais en
        blanc i un seguit d'imatges... B, tots els espais estan en blanc? Que haurem de fer?\r\n
        Si us trobeu molt perduts i perdudes, podeu demanar ajuda al vostre GameMaster per atenci... El
        GameMaster us estar observant en tot moment i ell decidir fins a quin punt us ajudar... Molta sort!`,

    },
    {
        desc: `Heu aconseguit passar al segon pis de la missi.\r\n
        Que voldr dir aquesta grfica? I quin text ms llarg, l'haurem de llegir per saber que hem de fer? ...
        T pinta que si... Potser compartir la informaci que ens proporcionen entre tots els membres de l'equip ens
        ajuda a trobar la soluci adient. Recordeu que es tracta de treballar en equip, no que part de l'equip ho
        solucioni i l'altra part copi... el GameMaster us observa...`,

    },
    {
        desc: `Enhorabona equip, heu aconseguit arribar al tercer pis d'aquesta aventura! \r\n
        Una barca? Un riu? 4 personatges? Crec que el  millor per comenar aquesta prova ser llegir les instruccions...\r\n
        Haureu de provar totes les opcions que us passin pel cap, per.. Necessitem eficcia i rapidesa! 
        Recordeu, el GameMaster est per ajudar-vos... Sempre que ell ho cregui necessari!`,

    },
    {
        desc: `Mxima atenci! Equip, heu arribat al quart pis de l'expedici. \r\n
        En aquesta sala ens trobem un panell ple de colors i de paraules... Qu vol dir tot aix? Quina connexi hem de trobar? 
        Us aconsellem, una vegada ms, que us comuniqueu com a equip i busqueu una soluci conjuntament...\r\n 
        El GameMaster us pot ajudar, per si veu que aneu molt b de temps, pot ser que faci tot el contrari... nims equip, ja gaireb ho teniu, fora!`,
    }
]

const Game = ({ numGame, handleFinish, userNumber }) => {
    switch (numGame) {
        case 1:
            return (
                <>
                    <Description val={activities[0].desc} />
                    <ActivityOrderImages handleFinish={handleFinish} />
                </>
            )
            break;
        case 2:
            return (
                <>
                    <Description val={activities[1].desc} />
                    <ActivityMathProblem handleFinish={handleFinish} />
                </>
            )
            break;
        case 3:
            return (
                <>
                    <Description val={activities[2].desc} />
                    <ActivityBoat handleFinish={handleFinish} />
                </>
            )
            break;
        case 4:
            return (
                <>
                    <Description val={activities[3].desc} />
                    <ActivityImage handleFinish={handleFinish} userNumber={userNumber} />
                </>
            )
            break;
    }
}

export default Game;

const Description = ({ val }) => (
    <div className="d-flex flex-column align-items-center mt-4">
        <p className="w-50 text-justify" style={{ whiteSpace: 'pre-line' }}>{val}</p>
    </div>
)